import React, { Component } from 'react'
import { Breadcrumb, Icon, Col, Row, Tag, Button, message } from 'antd'
import './detailidea.css'
import apis from './../../../../subpage/subapi'
class Detailidea extends Component {
  constructor(props) {
    super(props)
    this.state = {
      id: '',
      content: '',
      zuijiaArr: [],
      value:'',
      imageList:[]
    }
    this.handleChange = this.handleChange.bind(this);
  }
  componentWillMount() {
    this.setState({
      id: this.props.match.params.id
    })
  }
  componentDidMount() {
    // 
    // console.log(this.state.id);//
    console.log(this.state.content);
    
this.getData();
  
  }
  getData(){
    const access_token = window.sessionStorage.getItem('access_token')
    apis
      .get({ access_token, id: this.state.id }, 'backend/feedback/detail')
      .then(res => {
        console.log(res.content.replay_arr)
        if (res.code === 0) {
          this.setState({
            content: res.content,
            zuijiaArr: res.content.replay_arr,
            imageList:res.content.image
          })
        } else {
          message.error(res.message)
        }
      })
  }
  handleChange(event) {
    // console.log(event.target.value);
    
    this.setState({value: event.target.value});
  }
  handleAdd=()=>{
    console.log(this.state.id);
    console.log(this.state.value);
    const access_token = window.sessionStorage.getItem('access_token')
    apis
      .get({ access_token, id: this.state.id,content:this.state.value }, 'backend/feedback/add')
      .then(res => {
        console.log(res)
        if (res.code === 0) {
          this.setState({
           value:''
          },()=>{
            message.success(res.content) 
            this.getData();
          })
        } else {
          message.error(res.message)
        }
      })
    
  }
  render() {
    return (
      <div className="yijianxiangqing">
        <div className="yijianxiangqing-title">
          <div className="yijianxiangqing-title-wrapper">
            <Breadcrumb separator="-">
              <Breadcrumb.Item className="yijianxiangqing-pingjia">
                {/* <Icon
                  type="left"
                  style={{ marginLeft: 10, marginRight: 10 }}
                  onClick={() => {
                    console.log(11)
                    this.props.history.goBack(-1)
                  }}
                /> */}
                  <i className="iconfont icon-fanhui1" style={{ marginLeft: '0px', marginRight: '10px',fontSize:'18px' }} onClick={()=>{
                  console.log(11);
                  this.props.history.goBack(-1);
                }}></i>
                
              </Breadcrumb.Item>
              <Breadcrumb.Item></Breadcrumb.Item>
              <Breadcrumb.Item></Breadcrumb.Item>
            </Breadcrumb>
          </div>
        </div>
        <div className="yijianxiangqing-content" style={{ padding: '20px' }}>
          {/*   */}
          <div className="title">
            <h1 style={{ fontWeight: 600, fontSize: '30px' }}>
              
            </h1>
          </div>
          <div className="content1">
            <Row>
              <Col span={2}>
                :{' '}
                {this.state.content.is_reply === 1 ? 
                  <Tag color="#87d068"></Tag>
                : 
                (<Tag color="#f50"></Tag>)
                  
                }
              </Col>
              <Col span={2}>ID: {this.state.content.user_id}</Col>
              <Col span={2}>: {this.state.content.nick_name}</Col>
              <Col span={2}>: {this.state.content.mobile}</Col>
              <Col span={2}>: {this.state.content.community_name}</Col>
              <Col span={2}>: {this.state.content.from_type===2?"android":"ios"}</Col>
              <Col span={2}>: {this.state.content.version}</Col>
              {/* <Col span={3}></Col> */}
            </Row>
          </div>
          <div className="content1" style={{ marginTop: '20px' }}>
            <Row>
              <Col span={24}>
                {/* : */}
                : {this.state.content.content}
              </Col>
            </Row>
          </div>
          <div className="image-wrapper">
            {
              this.state.imageList.map((item,index)=>{
                return (
                  <img src={item} key={index}/>
                )
              })
            }
            {/* <img src={this.state.content.image} />
            <img src={this.state.content.image} />
            <img src={this.state.content.image} /> */}
          </div>
          <form >
          <div className="huifu-fankui">
            <div className="child1">:</div>
            <div className="child2">
              <textarea cols="190" rows="10" onChange={this.handleChange} value={this.state.value} />
            </div>
            <div className="zuijia">
              <Button
                style={{
                  borderRadius: '15px'
                }}
                onClick={this.handleAdd}
                type="primary"
              >
                
              </Button>
            </div>
          </div>
          </form>
          <div className="huifu-recored">
            <div className="huifu-title"></div>
            {this.state.zuijiaArr.map((item, index) => {
              return (
                <Row className="child1" style={{ marginTop: '10px' }} key={index}>
                  <Col span={1}>
                  <div
                    style={{
                      // width: '70px',
                      border:"1px solid #ccc",
                      borderRadius: '15px',
                      // paddingLeft:'5px',
                      // paddingRight:'5px',
                      // marginRight: '20px',
                      height: '28px',
                      lineHeight: '28px'
                    }}
                  >
                    
                  </div>
                  </Col>
                  <Col span={2}>
                  <div
                    style={{ textAlign: "center"}}
                    className="man"
                  >
                    :{item.reply_name}
                  </div>
                  </Col>
                  <Col span={18}>
                  {/* <div className='man' style={{width:'100px'}}></div> */}
                  <div className="content" style={{ marginRight: '20px' }}>
                    :{item.content}
                  </div>
                  </Col>
                </Row>
              )
            })}
            {/* <Row className="child1" style={{ marginTop: '10px' }}>
              <Button
                style={{
                  borderRadius: '15px',
                  padding: '1px',
                  marginRight: '20px',
                  height: '28px',
                  lineHeight: '17px'
                }}
              >
                
              </Button>
              <div
                className="man"
                style={{ marginRight: '20px', width: '100px' }}
              >
                :
              </div>
              
              <div className="content" style={{ marginRight: '20px' }}>
                :
              </div>
            </Row>
            <Row className="child1" style={{ marginTop: '10px' }}>
              <Button
                style={{
                  borderRadius: '15px',
                  padding: '1px',
                  marginRight: '20px',
                  height: '28px',
                  lineHeight: '17px'
                }}
              >
                
              </Button>
              <div
                className="man"
                style={{ marginRight: '20px', width: '100px' }}
              >
                :
              </div>
            
              <div className="content" style={{ marginRight: '20px' }}>
                :
              </div>
            </Row> */}
          </div>
        </div>
      </div>
    )
  }
}

export default Detailidea
